/** Exception due eslint doesn't support WAI-ARIA 1.3 that supports aria-description attr */
/* eslint-disable lit-a11y/aria-attrs */
import { classMap } from 'lit/directives/class-map.js';
import { defaultValue } from '../../internal/default-value';
import { html } from 'lit';
import { ifDefined } from 'lit/directives/if-defined.js';
import { live } from 'lit/directives/live.js';
import { property, query, state } from 'lit/decorators.js';
import { watch } from '../../internal/watch';
import componentStyles from '../../styles/component.styles';
import Icon from '../icon/icon.component';
import styles from './checkbox.styles';
import UIKitElement from '../../internal/uikit-element';
import type { CSSResultGroup } from 'lit';
import type { UIKitFormControl } from '../../internal/uikit-element';

/**
 * @summary Checkboxes allow the user to toggle an option on or off.
 * @documentation https://kind-desert-0fb80550f.5.azurestaticapps.net/?path=/docs/components-checkbox--docs
 * @status stable
 * @since 2.0
 *
 * @dependency koerber-icon
 *
 * @slot - The checkbox's label.
 *
 * @event koerber-blur - Emitted when the checkbox loses focus.
 * @event koerber-change - Emitted when the checked state changes.
 * @event koerber-focus - Emitted when the checkbox gains focus.
 * @event koerber-input - Emitted when the checkbox receives input.
 *
 * @csspart base - The component's base wrapper.
 * @csspart control - The square container that wraps the checkbox's checked state.
 * @csspart control--checked - Matches the control part when the checkbox is checked.
 * @csspart control--indeterminate - Matches the control part when the checkbox is indeterminate.
 * @csspart checked-icon - The checked icon, an `<koerber-icon>` element.
 * @csspart indeterminate-icon - The indeterminate icon, an `<koerber-icon>` element.
 * @csspart label - The container that wraps the checkbox's label.
 */
export default class Checkbox extends UIKitElement implements UIKitFormControl {
  static styles: CSSResultGroup = [componentStyles, styles];
  static dependencies = { 'koerber-icon': Icon };

  @query('input[type="checkbox"]') input: HTMLInputElement;

  @state() private hasFocus = false;

  @property() title = ''; // make reactive to pass through

  /** The name of the checkbox, submitted as a name/value pair with form data. */
  @property() name = '';

  /** The current value of the checkbox, submitted as a name/value pair with form data. */
  @property() value: string;

  /** Label of the checkbox. */
  @property() label: string;

  /** The checkbox's size. */
  @property({ reflect: true }) size: 'standard' | 'comfortable' = 'standard';

  /** Disables the checkbox. */
  @property({ type: Boolean, reflect: true }) disabled = false;

  /** Draws the checkbox in a checked state. */
  @property({ type: Boolean, reflect: true }) checked = false;

  /**
   * Draws the checkbox in an indeterminate state. This is usually applied to checkboxes that represents a "select
   * all/none" behavior when associated checkboxes have a mix of checked and unchecked states.
   */
  @property({ type: Boolean, reflect: true }) indeterminate = false;

  /** The default value of the form control. Primarily used for resetting the form control. */
  @defaultValue('checked') defaultChecked = false;

  /** Makes the checkbox a required field. */
  @property({ type: Boolean, reflect: true }) required = false;

  /**
   * Used to indicate that the input has some error based on some rule.
   */
  @property({ type: Boolean }) error = false;

  /** The component accessible label name */
  @property({ attribute: 'accessible-name', reflect: true }) accessibleName: string;

  /** The component accessible description */
  @property({ attribute: 'accessible-description', reflect: true }) accessibleDescription: string;

  private handleClick(event: Event) {
    event.preventDefault();
    this.checked = !this.checked;
    this.indeterminate = false;
    this.emit('koerber-change');
  }

  private handleWrapperClick(event: Event) {
    event.preventDefault();
    if ((event.target as HTMLElement).tagName !== 'INPUT') {
      this.checked = !this.checked;
      this.emit('koerber-change');
    }
  }

  private handleKeyDown(event: KeyboardEvent) {
    if (event.key === ' ') {
      event.preventDefault();
      this.checked = !this.checked;
      this.emit('koerber-change');
    }
  }

  public simulateSpacebarPress() {
    const event = new KeyboardEvent('keydown', {
      key: ' ',
      code: 'Space',
      keyCode: 32,
      charCode: 32,
      which: 32,
      bubbles: true,
      cancelable: true,
      composed: true
    });
    this.handleKeyDown(event);
  }

  private handleBlur() {
    this.hasFocus = false;
    this.emit('koerber-blur');
  }

  private handleInput() {
    this.emit('koerber-input');
  }

  private handleFocus() {
    this.hasFocus = true;
    this.emit('koerber-focus');
  }

  @watch(['checked', 'indeterminate'], { waitUntilFirstUpdate: true })
  handleStateChange() {
    this.input.checked = this.checked; // force a sync update
    this.input.indeterminate = this.indeterminate; // force a sync update
  }

  /** Simulates a click on the checkbox. */
  click() {
    this.input.click();
  }

  /** Sets focus on the checkbox. */
  focus(options?: FocusOptions) {
    this.input.focus(options);
  }

  /** Removes focus from the checkbox. */
  blur() {
    this.input.blur();
  }

  render() {
    //
    // NOTE: we use a <div> around the label slot because of this Chrome bug.
    //
    // https://bugs.chromium.org/p/chromium/issues/detail?id=1413733
    //
    return html`
      <div class="checkbox-wrapper" @click=${this.handleWrapperClick}>
        <label
          part="base"
          class=${classMap({
            checkbox: true,
            'checkbox--checked': this.checked,
            'checkbox--disabled': this.disabled,
            'checkbox--focused': this.hasFocus,
            'checkbox--indeterminate': this.indeterminate,
            'checkbox--standard': this.size === 'standard',
            'checkbox--comfortable': this.size === 'comfortable',
            'checkbox--error': this.error
          })}
        >
          <input
            class="checkbox__input"
            type="checkbox"
            title=${this.title /* An empty title prevents browser validation tooltips from appearing on hover */}
            name=${this.name}
            value=${ifDefined(this.value)}
            .indeterminate=${live(this.indeterminate)}
            .checked=${live(this.checked)}
            .disabled=${this.disabled}
            .required=${this.required}
            aria-checked=${this.checked ? 'true' : 'false'}
            aria-label=${ifDefined(this.accessibleName)}
            aria-description=${ifDefined(this.accessibleDescription)}
            @click=${this.handleClick}
            @input=${this.handleInput}
            @blur=${this.handleBlur}
            @focus=${this.handleFocus}
            @keydown=${this.handleKeyDown}
          />

          <span
            part="control${this.checked ? ' control--checked' : ''}${this.indeterminate
              ? ' control--indeterminate'
              : ''}"
            class="checkbox__control"
          >
            ${this.checked
              ? html`
                  <koerber-icon
                    part="checked-icon"
                    class="checkbox__checked-icon"
                    library="system"
                    name="check"
                    size="medium"
                  ></koerber-icon>
                `
              : ''}
            ${!this.checked && this.indeterminate
              ? html`
                  <koerber-icon
                    part="indeterminate-icon"
                    class="checkbox__indeterminate-icon"
                    library="system"
                    name="minus"
                    size="medium"
                  ></koerber-icon>
                `
              : ''}
          </span>

          <div part="label" class="checkbox__label">
            <slot>
              <koerber-form-field-label label="${this.label}"></koerber-form-field-label>
            </slot>
          </div>
        </label>
      </div>
    `;
  }
}
